import React from 'react';
import styled from 'styled-components'
import Header from '../../layouts/Header';

import ReviewFeed from '@pages/Users/CompanyProfile/components/ReviewFeed';
import AboutCompany from '@pages/Users/CompanyProfile/components/AboutCompany';


const TitleBox = styled.div`
    font-weight: 500;
    font-size: 20px;
    line-height: 30px;
    margin-bottom: 15px;
    margin-top: 15px;
`;


export default function Reviews() {
    return (
        <div className="main_container">
            <Header />
            <div className="container">
                <TitleBox>Reviews</TitleBox>
                <div className="container">
                    <div className="row">
                        <div className="col-md-7">
                            <ReviewFeed />
                            <ReviewFeed />
                            <ReviewFeed />
                            <ReviewFeed />
                            <ReviewFeed />
                            <ReviewFeed />
                            <ReviewFeed />
                            <ReviewFeed />
                            <ReviewFeed />
                            <ReviewFeed />
                            <ReviewFeed />
                            <ReviewFeed />
                            <ReviewFeed />
                            <ReviewFeed />
                        </div>
                        <div className="col-md-5">
                            <AboutCompany />
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}
