import React, { useState, useEffect } from 'react';
import SojiBaseInit from '@modules/SojiBase';
import PagePreloader from '@components/preloader/PagePreloader';
import DataTable from '@modules/DataTable';
import getColumns from './Table/Column';
import styled from 'styled-components';
import Header from '../../layouts/Header';

const TitleBox = styled.div`
    font-weight: 500;
    font-size: 20px;
    line-height: 30px;
    margin-bottom: 15px;
    margin-top: 15px;
`;

export default function RecentDocument() {
    const [state, setState] = useState({
        fetchedData: false,
    });

    const stateHandler = (key, value) => {
        state[key] = value;
        setState({ ...state });
    };

    useEffect(() => {
        (async () => {
            // const SojiBase = await SojiBaseInit;
            // const getter = await SojiBase.get('v1/suspected_users/get-all_recent_documents');
            // getter.on('static', async (response) => {
            //     const data = response.response.data;
            //     stateHandler('fetchedData', data);
            // });
            stateHandler('fetchedData', []);
        })();
    }, []);

    return (
        <div className="main_container">
            <Header />
            <div className="container">
                <div className="container content_Container">
                    {state.fetchedData ? (
                        <>
                            <TitleBox>Documents</TitleBox>
                            <DataTable getColumns={getColumns} MOCK_DATA={state.fetchedData} texts="No Results fo" />
                        </>
                    ) : (
                        <PagePreloader />
                    )}
                </div>
            </div>
        </div>
    );
}
